import React from "react";
import { Slider, Service, Contact, Reviews } from "../../modules/Home";
import { ProductCategorySection } from "../../components/core";
import { useQuery } from "@apollo/client";
import { getAllProductsByCategory } from "../../graphql/schema/product.graphql";
import Loader from "../../components/core/Loader";
import { Helmet } from "react-helmet";

function Home() {
  const pet = useQuery(getAllProductsByCategory("thucung"));
  const food = useQuery(getAllProductsByCategory("thucanthucung"));
  return (
    <>
      <Helmet>
        <title>Thú cưng sen shop thủ đức</title>
        <meta
          name="description"
          content="Thú cưng sen shop cung cấp các loại thức ăn, phụ kiện chất lượng khu vực thủ đức. Sen shop thủ đức mang đến các loại dịch vụ hài giúp thú cưng vui vẻ, khách hàng hài lòng đem lại trải nghiệm tốt cho khách hàng."
        />
        <link rel="canonical" href="https://senshop.tech/" />
        <meta property="og:type" content="website" />
        <meta property="og:title" content="Thú cưng sen shop thủ đức" />
        <meta property="og:url" content="https://senshop.tech/" />
        <meta
          property="og:image"
          content="https://cdn.senshop.tech/Blog/SenShop.jpg"
        />
        <meta
          property="og:description"
          content="Thú cưng sen shop cung cấp các loại thức ăn thú cưng, phụ kiện chất lượng khu vực thủ đức. Sen shop thủ đức mang đến các loại dịch vụ hài giúp thú cưng vui vẻ, khách hàng hài lòng đem lại trải nghiệm tốt cho khách hàng."
        />
      </Helmet>
      <Slider />
      <Service />
      {pet.loading ? (
        <Loader />
      ) : (
        <ProductCategorySection
          productList={pet.data.getAllProductsByCategory}
          categoryList={[
            "Chó cảnh",
            "Mèo cảnh",
            "Thú kiểng",
            "Hamster",
            "Thú cưng khác",
          ]}
          sectionName="Thú cưng"
        />
      )}

      {food.loading ? (
        <Loader />
      ) : (
        <ProductCategorySection
          productList={food.data.getAllProductsByCategory}
          categoryList={["Thức ăn cho chó", "Thức ăn cho mèo", "Thức ăn khác"]}
          sectionName="Thức ăn cho thú cưng"
        />
      )}
      <Reviews />
      <Contact />
    </>
  );
}

export default Home;
